@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.export {
  position:'fixed';
  right: '4vw';
  height: '2vw'; 
  display:'flex'; 
  align-items:'center'; 
  top: '5vw';
  width: auto;
  border: 1px solid grey;
  background: #0e419d;
  padding: 0.6vw 2vw;
  border-radius: $font_xsmall;
  color: $primary_white;
}
.ratechart {
  position: relative;
  width: calc(100% - 18vw);
  .disabled-button {
    opacity: 0.5;
  }
  .MuiPaper-root {
    padding: 1vw;
    background-color: #d2e3fc;
  }
  .form-label {
    font-size: 0.9vw;
    margin-bottom: 0 !important;
  }
  &__container {
    @include flexColumnACenter;
    background: $bg_light_grey;
    height: 90%;
  }
  &__header {
    width: 100%;
    position: sticky;
    top: 0;
    z-index: 999;
    padding: $font_xverysmall $font_xverysmall;
    @include flexColumnACenter;
    background: $primary_white;
    &__section {
      width: 100%;
      display: flex;
      justify-content: space-between;
      &__main {
        padding: 0 0 0.3vw 0;
        @include flexbetween;
        flex-direction: column;
        h4 {
          font-size: $font_button;
        }
        h5 {
          font-size: $font_verysmall;
          color: $font_grey;
          font-weight: 400;
        }
      }
      &__bottom {
        width: 14vw;
      }
    }
    border-bottom: 1px solid #0e419d;
  }
  &__table {
    padding-top: 30px;
    width: 98%;
    height : 98%;
    &__header {
      padding: 0 0 $font_xlarge 0;
      &__section {
        @include flexbetween;
        input {
          width: 50%;
        }
        button {
          width: auto;
          border: 1px solid grey;
          background: #0e419d;
          padding: 0.6vw 2vw;
          border-radius: $font_xsmall;
          color: $primary_white;
        }
      }
    }
    .striped-table tbody tr td:first-child {
      color: red !important;
    }
    .striped-table tbody tr,
    th {
      background-color: transparent;
      font-size: $font_xsmall !important;
      color: $primary_white;
    }
    .striped-table tbody tr,
    th:nth-child(1) {
      color: $primary_white !important;
      background: #0e419d !important;
    }
    .striped-table tbody tr:nth-child(odd) {
      background-color: #ffff;
    }
    .striped-table tbody tr:nth-child(even) {
      background-color: #d2e3fc;
    }
    thead {
      position: sticky;
      top: 0;
      z-index: 1;
      background: #0e419d;
      color: $primary_white;
    }
  }
  .col-lg-2 {
    margin-bottom: 1.5vw;
  }
  .col-lg-3 {
    margin-bottom: 1vw;
  }
  &__popup {
    position: absolute;
    top: 5.9vw;
    width: 100%;
    @include flexJCenter;
    background: $bg_light_grey;
    &__container {
      width: 90%;
    }
  }
}

.loading-spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
}

.print{
  width: 2vw;
  height: 2vw;
  position: absolute;
  right: 1vw;
  top: 5vw;
}