@import "../../../styles/variables.scss";
@import "../../../styles/mixins.scss";

.download {
    width: 2vw;
    height: 2vw;
    position: absolute;
    right: 1vw;
    top: 1vw;
}

.empty_data {
    height: -webkit-fill-available;
    display: flex;
    align-items: center;
    justify-content: center;

    h1 {
        text-align: center;
    }

    h3 {
        text-align: center;
    }
}

.back {
    position: absolute;

    img {
        width: 2vw;
        height: 2vw;
    }
}

.payment {
    display: flex;
    flex-direction: column;
    // width: calc(100% - 18vw);
    overflow-y: scroll;
    font-family: 'Times New Roman';
    padding-right: 5%;
    padding-left: 5%;

    &__header {

        &__heading {
            @include flexJCenter;

            p {
                font-family: 'Times New Roman';
                font-size: 1em;
                font-weight: 500;
                padding-top: 1vw;
            }
        }

        &__bottomheading {
            @include flexJCenter;
            border-bottom: 2px solid black;

            p {
                font-family: 'Times New Roman';
                margin: 0.3vw;
                font-size: 1em;
                margin-right: 1em;
                margin-left: 1vw;
            }
        }

        &__secondbottomheading {
            @include flexJCenter;
            justify-content: space-around;

            p {
                font-family: 'Times New Roman';
                margin: 0.3vw;
                font-size: 1em;
                margin-right: 1em;
                margin-left: 1vw;
            }
        }
    }

    &__table {
        @include flexJCenter;
        margin-bottom: 1vw;

        &__header {
            border-bottom: 2px solid black;
            display: flex;
            justify-content: space-between;

            p {
                font-family: 'Times New Roman';
                font-size: 1em;
                margin: 0.3vw;
                width: 25%;
                text-align: center;
            }
        }

        &__content {
            padding-top: 10px;
            padding-bottom: 10px;
            border-bottom: 2px solid black;
            display: flex;
            justify-content: space-between;

            p {
                font-family: 'Times New Roman';
                font-size: 1em;
                margin: 0.3vw;
                width: 25%;
                text-align: center;
            }

        }

        &__belowcontent {
            padding-top: 10px;
            padding-bottom: 10px;
            border-bottom: 2px solid black;
            display: flex;
            justify-content: space-between;

            p {
                font-family: 'Times New Roman';
                font-size: 1em;
                margin: 0.3vw;
                width: 20%;
                text-align: center;
                font-weight: bold;
            }
        }
    }

    &__footer {
        align-items: flex-end;
        margin-left: auto;

        margin-right: 0 p {
            font-family: 'Times New Roman';
            font-size: $font_xsmall;
            margin: 0.3vw;
            text-align: center;
        }
    }
}