@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.dashboard {
  position: relative;
  width: calc(100% - 18vw);
  .disabled-button {
    opacity: 0.5;
  }
  .Cbody {
    width: 95%;
    margin-top: 1vw;
  }
  .MuiPaper-root {
    padding: 1vw;
    background-color: #d2e3fc;
  }
  &__container {
    @include flexColumnACenter;
    background: $bg_light_grey;
    height: 100%;
    overflow-y: scroll;
  }
  &__header {
    width: 100%;
    position: sticky;
    top: 0;
    z-index: 999;
    padding: $font_xverysmall $font_xverysmall;
    @include flexColumnACenter;
    background: $primary_white;
    &__section {
      width: 100%;
      display: flex;
      justify-content: space-between;
      &__main {
        padding: 0 0 0.3vw 0;
        @include flexbetween;
        flex-direction: column;
        h4 {
          font-size: $font_button;
        }
        h5 {
          font-size: $font_verysmall;
          color: $font_grey;
          font-weight: 400;
        }
      }
      &__bottom {
        width: 14vw;
      }
    }
    border-bottom: 1px solid #0e419d;
  }
  &__table {
    padding: $font_regular 0;
    width: 98%;

    &__header {
      padding: 0 0 $font_xlarge 0;
      &__section {
        @include flexbetween;
        input {
          width: 25%;
        }
        button {
          width: auto;
          border: 1px solid grey;
          background: #0e419d;
          padding: 0.6vw 2vw;
          border-radius: $font_xsmall;
          color: $primary_white;
        }
      }
    }

    .striped-table tbody tr td,
    th {
      background-color: transparent;
      font-size: $font_xsmall !important;
    }
    .striped-table tbody tr:nth-child(odd) {
      background-color: #ffff;
    }
    .striped-table tbody tr:nth-child(even) {
      background-color: #d2e3fc;
    }
    thead {
      position: sticky;
      top: 0;
      z-index: 1;
      background: #8ab4f8;
    }
  }
  .col-lg-4 {
    margin-bottom: 0.5vw !important ;
  }

  .form-label {
    font-size: 0.9vw;
    margin-bottom: 0 !important;
  }
  &__popup {
    position: absolute;
    top: 5.9vw;
    width: 100%;
    @include flexJCenter;
    background: $bg_light_grey;
    &__container {
      width: 90%;
    }
  }
}

.empty_data {
  font-family: "Times New Roman";
  height: -webkit-fill-available;
  display: flex;
  align-self: center;
  align-items: center;
  justify-content: center;

  h1 {
    font-family: "Times New Roman";
    text-align: center;
  }

  h3 {
    font-family: "Times New Roman";
    text-align: center;
  }
}

.charts-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;
  width: 100%;
  margin-top: 10px;

  .vertical-div-group {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-around;

    .vertical-div {

      @media screen and (max-width: 768px) {
        width: 100%;
      }
    }
  }

  .horizontal-div {
    margin-top: 10px;
    @media screen and (max-width: 768px) {
      width: 100%; // Full width on smaller screens
    }
  }
}
